section#favorites {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
section#favorites .content {
  margin: 40px auto 0 auto;
  width: 1000px;
  max-width: 100%;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
}
section#favorites .content .list-wrapper .section-header {
  color: #1b1b1b;
}
ul {
  margin-top: 30px;
  width: 100%;
  box-shadow: 0 0.0625rem 0.125rem 0 rgba(0, 0, 0, 0.15);
  background: #fbfbfb;
  border-radius: 8px;
  align-items: center;
  padding: 1rem;
}

ul .ul-header {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 0.7rem 0.7rem 0.7rem;
  border-bottom: 1px solid #cdcdcd;
}
ul .ul-header .header-index {
  color: #888;
  font-weight: 300;
}

ul .ul-header .header-actions input {
  margin-right: 20px;
}
ul .ul-header .header-actions button {
  border: 0;
  background: #3483fa;
  color: white;
  padding: 0.7rem 2rem;
  border-radius: 4px;
  cursor: pointer;
  &:disabled {
    cursor: default;
    background-color: #ddd;
    color: #aaa;
  }
}
ul li {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 2rem 0.7rem;
  border-bottom: 1px solid #cdcdcd;
  & input {
    margin-right: 20px;
  }
  &:last-child {
    border: 0;
  }
}
ul li .li-content {
  width: 650px;
  max-width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}
ul li .li-content .li-photo {
  width: 120px;
  height: 84px;
  margin-right: 10px;
  cursor: pointer;
  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
ul li .li-content .li-details {
  padding: 1rem;
  & .li-title {
    font-size: 22px;
    cursor: pointer;
  }
  & .li-title,
  .li-amount {
    color: #444;
    font-weight: 300;
  }
  & .li-price {
    font-size: 20px;
    font-weight: bold;
    color: #000;
  }
  & * {
    margin-bottom: 5px;
  }
}
ul li .li-actions {
  display: flex;
  flex-direction: row;
  align-items: center;
}

ul li .li-actions button {
  border: 0;
  background: #3483fa;
  color: white;
  padding: 0.7rem 2rem;
  border-radius: 4px;
  cursor: pointer;
  font-weight: regular;
  transition: all 0.1s ease-in-out;
  &:hover {
    filter: brightness(110%);
  }
  &:disabled {
    cursor: default;
    background-color: #ddd;
    color: #aaa;
    &:hover {
      filter: none;
    }
  }
}
ul li .li-actions button:disabled {
}
